@use "../../index"as base;

.about {
    background: white;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    display: flex;
}

@keyframes add-stroke {
    to {
        stroke-dashoffset: 0px;
    }
}

@keyframes fill-svg {
    from {
        fill: transparent;
    }

    to {
        fill: black;
    }
}

.introduction {
    display: flex;
    flex-direction: column;
    flex: 3.5;
    align-items: center;
    align-content: center;
    justify-items: center;
    justify-content: center;
    font-family: base.$styledFontFamily;

    @extend .unselecteable;

    &--container {
        background-color: rbga(7, 71, 100, 0.9);
        padding: 20px;
        z-index: 13;
    }

    &-name {
        font-size: base.$largeFontSize;

        & svg {
            width: #{13 * 5}vw;
            height: 5vw;
        }

        &:first-child {
            fill: transparent;
            animation: fill-svg 0.5s ease forwards 4.5s;
        }

        @for $i from 1 through 13 {
            & path:nth-child(#{$i}) {
                stroke-dasharray: 400px;
                stroke-dashoffset: 400px;
                animation: add-stroke 3s ease forwards #{($i*3)/10}s;
            }
        }
    }

    &-designation {
        font-size: base.$fontSize;

        & svg {
            width: #{13 * 3}vw;
            height: 3vw;
        }

        @for $i from 1 through 16 {
            & path:nth-child(#{$i}) {
                stroke-dasharray: 466px;
                stroke-dashoffset: 466px;
                animation: add-stroke 2s ease forwards #{4 + ($i*3)/10}s;
            }
        }
    }
}

.waves {
    position: absolute;
    width: 100%;
    height: 100vh;
    bottom: 0;
    left: 0;
    /*Fix for safari gap*/
    min-height: 100px;
    max-height: 150px;
}

.parallax {
    &>use {
        animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;

        @for $i from 1 through 4 {
            &:nth-child(#{$i}) {
                animation-delay: -#{$i + 1}s;
                animation-duration: #{4 + $i * 3}s;
            }
        }
    }
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

/*Shrinking for mobile*/
@media (max-width: 768px) {
    .content {
        height: 30vh;
    }

    h1 {
        font-size: 24px;
    }
}


.jelly-container {
    width: 100%;
    position: absolute;
    height: 500px;
    overflow: hidden;
    background: linear-gradient(to bottom, rgba(0, 141, 210, 0.63) 0%, rgba(0, 0, 0, 0) 100%);
}

.jelly-wrapper {
    width: 265px;
    position: absolute;
    transform: translate3D(-50%, -75%, 0);
    animation: jelly-movement 10s infinite linear;
}

.jelly-hair {
    height: 75px;
    width: 100px;
    background: linear-gradient(to bottom, rgba(239, 137, 26, 1) 0%, rgba(239, 137, 26, 1) 20%, rgba(235, 242, 138, 1) 100%);
    /* W3C */
    ;
    position: absolute;
    left: 29%;
    transform: rotate3d(0, 1, 0, 180deg) translate3d(28%, -15px, 0) rotate3d(0, 0, 1, 75deg);
    border-radius: 25px;
    animation: hair-movement 10s infinite linear;
}

.jelly-body {
    background: linear-gradient(to bottom, rgba(0, 141, 210, 1) 0%, rgba(235, 242, 138, 1) 100%);
    /* W3C */
    ;
    position: relative;
    height: 250px;
    width: 100%;
    overflow: hidden;
    z-index: 1;
    animation: head-movement 10s infinite linear;
}

.jelly-inner {
    position: absolute;
    left: 50%;
    top: 35%;
    animation: facing-movement 10s infinite linear;
}

%jelly-eye {
    content: '';
    position: absolute;
    height: 15px;
    bottom: -25px;
    width: 15px;
    border-radius: 50%;
    background-color: #FFF;
    animation: eyes-blink 3.5s infinite linear;
}

.jelly-eyes {
    position: absolute;
    top: -40px;
    animation: eyes-movement 10s infinite linear;

    &:before {
        @extend %jelly-eye;
        left: 60px;
    }

    &:after {
        @extend %jelly-eye;
        left: -70px;
    }
}

%jelly-hand {
    content: '';
    position: absolute;
    height: 50px;
    width: 45px;
    border-top: 0;
    border-radius: 0 0 50% 50%;
    box-shadow: 0px 5px 6px #3B949B;
}

.jelly-hands {
    position: absolute;
    top: 65px;

    &:before {
        @extend %jelly-hand;
        left: 110px;
    }

    &:after {
        @extend %jelly-hand;
        left: -155px;
    }
}

.jelly-mouth {
    position: absolute;
    height: 80px;
    width: 200px;
    left: 50%;
    top: 35%;
    transform: translate3D(-50%, 0, 0);
    background-color: #F4CFD1;
    border-radius: 30px;
    box-shadow: 0px 0px 7px #808080;

    &:after {
        content: '';
        position: absolute;
        height: 3%;
        width: 60%;
        background-color: #FF9393;
        left: 50%;
        top: 50%;
        transform: translate3D(-50%, 0, 0);
        box-shadow: 0 0 3px #FF6B6B;
        animation: mouth-movement 10s infinite linear;
    }
}

.jelly-tentacle {
    height: 50px;
    width: 50px;
    position: absolute;
    background-color: rgba(235, 242, 138, 1);
    border-radius: 0 0 25px 25px;
    box-shadow: 0px 2.5px 0px #008D9B;
}

.jelly-tentacle-1 {
    left: 0%;
    animation: tentacle-animation 1s infinite cubic-bezier(0.46, 0.2, 0.53, 0.92) -.2s;
}

.jelly-tentacle-2 {
    left: 25%;
    transform: translate3D(-25%, 0, 0);
    animation: tentacle-animation 1s infinite cubic-bezier(0.46, 0.2, 0.53, 0.92) -.4s;
}

.jelly-tentacle-3 {
    left: 50%;
    transform: translate3D(-50%, 0, 0);
    animation: tentacle-animation 1s infinite cubic-bezier(0.46, 0.2, 0.53, 0.92) -.1s;
}

.jelly-tentacle-4 {
    right: 25%;
    transform: translate3D(25%, 0, 0);
    animation: tentacle-animation 1s infinite cubic-bezier(0.46, 0.2, 0.53, 0.92) -.7s;
}

.jelly-tentacle-5 {
    right: 0%;
    animation: tentacle-animation 1s infinite cubic-bezier(0.46, 0.2, 0.53, 0.92) -.3s;
}

.jelly-shadow {
    position: absolute;
    height: 15px;
    border-radius: 50%;
    background-color: rgba(134, 134, 134, 0.23);
    box-shadow: 0 0 10px rgba(134, 134, 134, 0.23);
    bottom: 5%;
    transform: translate3d(-50%, 0, 0);
    animation: shadow-movement 10s infinite linear;
}

.bubble {
    height: 1px;
    width: 1px;
    position: absolute;
    background-color: rgba(0, 141, 210, 0.3);
    border-radius: 50%;
    transform: translateX(-50%);

    &:after {
        content: '';
        position: absolute;
        height: 85%;
        width: 85%;
        border-top: 1px solid rgba(255, 255, 255, 0.63);
        border-radius: 50%;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%, -50%, 0) rotateZ(-45deg);
    }
}

